'use strict';

const wall = document.body.querySelector('.wall');
const spider = document.body.querySelector('.spider');

spider.style.position = 'absolute';
wall.style.position = 'relative'; // Контейнер для позиционирования паука

document.addEventListener('click', (e) => {
  const wallRect = wall.getBoundingClientRect(); // Получаем координаты стены

  const clickX = e.clientX - wallRect.left; // Относительные координаты клика
  const clickY = e.clientY - wallRect.top;

  const spiderWidth = spider.clientWidth; // Ширина паука
  const spiderHeight = spider.clientHeight; // Высота паука

  // Корректируем координаты, чтобы центр паука был там, где клик
  let finalX = clickX - spiderWidth / 2;
  let finalY = clickY - spiderHeight / 2;

  // Ограничиваем координаты, чтобы паук не выходил за пределы стены
  finalX = Math.max(0, Math.min(finalX, wall.clientWidth - spiderWidth));
  finalY = Math.max(0, Math.min(finalY, wall.clientHeight - spiderHeight));

  spider.style.left = finalX + 'px';
  spider.style.top = finalY + 'px';

  console.log(`Spider moved to X: ${finalX}, Y: ${finalY}`);
});
